.Record{
    clear: both;
    width: 100%;
    height: 900px;
    .record{
        width: 100%;
        margin-top: 50px;
        .record_title{
            h3{font-family: 'DoHyeonRegular';}
            width: 100%;
            font-size: 3.2rem;
            font-weight: 700;
            text-align: center;
            margin: 25px 0;
        }
        .record_area{
            width: 100%;
            .contents{
                width: 100%;
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                margin: 50px 0;
                .qa{
                    width: calc(100%/3);
                    margin: 0 30px;
                    padding: 30px;
                    border: 1px solid #ccc;
                    background: #fff;
                    .q{
                        font-size: 2.4rem;
                        margin-bottom: 10px;
                    }
                    .q::before{
                        content: " Q ";
                        color: #356edc;
                        font-family: DoHyeonRegular;
                        font-size: 3.2rem;
                    }
                    .a{
                        p{
                            margin: 5px 0;
                            font-size: 1.6rem;
                        }
                    }
                    
                }
            }
        }
        .record_div_area{
            display: flex;
            justify-content: space-around;
            align-items: center;
            color: #4C4C4C;
            .contents{
                display: flex;
                align-items: center;
                justify-content: space-between;
                font-size: 1.4rem;
                width: 100%;
                .content_left{
                    width: 10%;
                    font-size: 2.8rem;
                    margin: 0 40px;
                    h3{font-family: 'DoHyeonRegular';}
                }
                .content_right{
                    width: 90%;
                    text-align: center;
                    display: flex;
                    flex-direction: column;
                    img{width: 200px;}
                }
            }
        }
    }
    a{
        color: #4C4C4C;
    }
    a:hover{
        color:mediumblue;
    }
}

/* 태블릿 */
@media screen and (max-width: 1310px) {
    .Record{
        height: 1300px;
        .record{
            .record_area{
                .contents{
                    flex-direction: column;
                    align-items: center;
                    padding: 0;
                    .qa{
                        width: calc(100% - 180px);
                        margin: 10px 0;
                        border: 1px solid #ccc;
                        background: #fff;
                        .q{
                            margin-bottom: 10px;
                        }
                        .q::before{
                            content: " Q ";
                            color: #356edc;
                            font-family: DoHyeonRegular;
                        }
                        p{
                            margin: 5px 0;
                        }
                    }
                }
            }
            .record_div_area{
                width: 100%;
                .contents{
                    padding: 0;
                    .content_left{
                        text-align: left;
                    }
                    .content_right{
                        img{width: 70%;}
                    }
                }
            }
        }
    }
}

/* 모바일 */
@media screen and (max-width: 767px) {
    .Record{
        height: 1700px;
        .record{
            .record_area{
                .contents{
                    width: 100%;
                    .qa{
                        width: 70%;
                        margin: 10px 0;
                        border: 1px solid #ccc;
                        background: #fff;
                        .q{
                            margin-bottom: 10px;
                        }
                        .q::before{
                            content: " Q ";
                            color: #356edc;
                            font-family: DoHyeonRegular;
                        }
                        p{
                            margin: 5px 0;
                        }
                    }
                }
            }
            .record_div_area{
                width: 100%;
                flex-direction: column;
                .contents{
                    margin: 20px 0;
                    img{width: 70%;}
                }
            }
        }
    }
}

@media screen and (max-width: 376px) {
    .Record{
        height: 1750px;
    }
}
