.Skills{
    clear: both;
    width: 100%;
    height: 800px;
    box-shadow : 0 4px 6px -1px rgba(0,0,0,0.1),0 2px 4px -1px rgba(0,0,0,0.06);
    .contents{
        .skill-chart{
            width: 100%;
            height: 100%;
            margin-top: 100px;
            display: flex;
            justify-content: space-around;
            .item-box{
                width: calc((100% - 40px)/3);
                height: auto;
                text-align: center;
                padding: 0 20px;
                .item-label{font-family: "DoHyeonRegular";font-size: 3rem;}
                .item{
                    display: flex;
                    flex-direction: column;
                    width: 60%;
                    margin: 30px auto;
                    .chartList{
                        width: 100%;
                        .chart{
                            width: 100%;
                            position: relative;
                            margin: 10px 0;
                            float: left;
                            .chartName{width: 30px; font-size: 1.2rem;float: left;}
                            .percent{
                                width: calc(100% - 38px);
                                height: 10px;
                                float: left;
                                position: relative;
                                left: 30px;                                
                                margin: 2.5px;
                                .chartTotal{
                                    position: absolute;
                                    top: 0;
                                    width: 100%;
                                    height: 100%;
                                    background-color: #D8D8D8;
                                    border-radius: 10px;
                                }
                                .chartData{
                                    position: absolute;
                                    top: 0;
                                    height: 100%;
                                    background-color: #4641D9;
                                    border-radius: 10px;
                                }
                            }                            
                        }
                    }
                }
            }
        }
        .iconArea{
            width: 100%;
            padding: 80px 0 0 0;  
            display: flex;          
            justify-content: space-between;
            .iconBox{
                background-color: #fff;
                border-radius: 50%;
                overflow: hidden;
                float: left;
                margin: 0 10px;
                width: calc(100% / 13 - 40px);
                padding: 10px;
                display: flex;
                justify-content: center;
                align-items: center;
                box-shadow : 0 4px 6px -1px rgba(0,0,0,0.1),0 2px 4px -1px rgba(0,0,0,0.06);
                img{
                    width: 70%;
                }
            }
        }
    }
}
/* 너무 작아져서 아이콘 따로 별도 처리 */
@media screen and (max-width: 1423px) {
    .Skills{
        height: 900px;
        .contents{
            .iconArea{
                padding: 40px 0 0 0;                
                flex-wrap: wrap;
                align-content: stretch;
                .iconBox{
                    margin: 10px;
                    width: calc(100% / 10 - 10px);
                }
            }
        }
    }
}

/* 태블릿 */
@media screen and (max-width: 1023px) {
    .Skills{
        height: 1350px;
        .contents{
            .skill-chart{
                margin-top: 30px;
                flex-direction: column;
                .item-box{
                    width: 100%;
                    margin: 0 auto;
                    flex-direction: row;
                    padding: 0;
                    .item{
                        display: flex;
                        flex-direction: row;
                        .chartList{                            
                            .chart{          
                                .chartName{width: 25px;}
                            }
                        }
                    }
                }
            }
            .iconArea{
                .iconBox{
                    width: calc(100% / 8 - 10px);
                }
            }
        }
    }
}

/* 모바일 */
@media screen and (max-width: 767px) {
    .Skills{
        height: 1550px;
        .contents{
            .iconArea{
                .iconBox{
                    width: calc(100%/5);
                }
            }
        }
    }
}

@media screen and (max-width: 450px) {
    .Skills{
        height: 1400px;
    }
}