.AlertPopup{
    width: 300px;
    height: 40px;
    line-height: 40px;
    position: fixed;
    bottom: 40px;
    left: 50%;
    transform: translate(-50%, 0);
    color: #fff;
    background-color: rgb(33, 33, 33, 0.5);
    border-radius: 50px;
    transition: all 2s;
    text-align: center;
    z-index: 9999;
    #msg{color: #fff;}
}