.Careers{
    clear: both;
    width: 100%;
    height: 650px;
    background: #fff;
    text-align: center;
    small{color: #aaa;}
    .careers{
        margin-top: 50px;
        .career_list{
            display: flex;
            flex-direction: row;
            justify-content: space-around;
            .node{
                cursor: pointer;
                width: calc(100%/3);
                text-align: center;
                font-size: 2rem;
                padding: 20px;
                box-shadow : 0 4px 6px -1px rgba(0,0,0,0.1),0 2px 4px -1px rgba(0,0,0,0.06);
            }
            .node:hover{
                background: #F6F6F6;
            }
        }
        .career_area{
            h2{font-size: 3rem; margin-top: 30px;}
            text-align: left;
            .career_contents{
                .swiper-slide{
                    height: 300px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    dl{
                        transition: all 200ms linear;
                        transform: scale(0.8);
                        box-shadow : 0 4px 6px -1px rgba(0,0,0,0.1),0 2px 4px -1px rgba(0,0,0,0.06);
                        color: #434;
                        margin: 5px;
                        dt{
                            font-weight: 700;
                            font-size: 2rem;
                            padding: 3px 10px 3px 5px;
                        }
                        dd{
                            font-size: 1.8rem;
                            margin: 2px 0 0 0;
                            padding: 3px 5px;
                        }
                        dd.techStack{
                            text-decoration: underline;
                            font-size: 1.6rem;
                        }
                        dd.techStack::before{
                            content: "* ";
                            text-decoration: none;
                        }
                    }
                    &.swiper-slide-active{
                        font-size: 2rem;
                        dl{
                            transform: scale(1);
                            border: 1px solid #ccc;
                            color: #000;
                            padding: 0;
                            dt{                                
                                background: #4641D9;
                                color: #fff;
                                font-size: 1.8rem;
                            }
                            dd{
                                font-size: 1.4rem;
                                padding: 3px 5px 3px 15px;
                            }
                        }
                    }
                }
            }
        }
        h2{
            color: #4641D9;
            text-align: center;
        }
        .emphasis{
            color: #DB0000;
        }
        #popupDom{
            .popup{
                position: fixed;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 300px;
                height: 300px;
                background: #fff;
                box-shadow : 0 4px 6px -1px rgba(0,0,0,0.1),0 2px 4px -1px rgba(0,0,0,0.06);
                color: #999;
                z-index: 9999;
                .popup_wrapper{
                    position: relative;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    .title{
                        font-size: 2rem;
                        color: #4641D9;
                        padding: 5px 0;
                    }
                    .body{
                        text-align: left;
                        padding: 10px;
                        ul{
                            li{font-size: 1.6rem;}
                            li:before{
                                content: "- ";
                            }
                        }
                    }
                }
            }
        }
        .layer{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba(213, 213, 213, 0.7);
            z-index: 9998;
        }       
    }
    button{
        cursor: pointer;
        border: none;
        background: #fff;
        padding: 5px 10px;
        box-shadow : 0 4px 6px -1px rgba(0,0,0,0.1),0 2px 4px -1px rgba(0,0,0,0.06);
    }
}

ul, li{list-style: none;}
