/* 모바일 */
@media screen and (max-width: 767px) {
    .Careers{
        height: 650px;
        .careers{
            .career_list{
                .node{
                    font-size: 1.6rem;
                }
            }
            .career_area{
                .career_contents{
                    .swiper-slide{
                        dl{padding: 5px;}
                    }
                }
            }
        }           
    }
}

