.Top{
    width: calc(100% - 60px);
    height: 80px;
    padding: 10px 30px;
    background: #eee;
    float: left;
    .leftTop{
        width: 35%;
        height: 100%;
        float: left;
        h2{
            height: 100%;
            font-size: 2.4rem;
            line-height: 80px;
            text-align: left;
            color: #4C4C4C;
        }
    }
    .rightTop{
        width: 65%;
        height: 100%;
        float: left;
    }
}
.Top.fixed{
    position: fixed;
    z-index: 9999;
    border-bottom: 1px solid #ccc;
    background: #fff;
    border-radius: 0 0 15px 15px;
}

/* 태블릿 */
@media screen and (max-width: 1023px) {
    .Top{
        width: 100%;
        height: 80px;
        .leftTop{
            width: 20%;
        }
        .rightTop{
            width: 80%;
            height: 100%;
            float: left;
        }
    }
}
/* 태블릿 */
@media screen and (max-width: 1023px) {
    .Top{
        padding: 0;
        .leftTop{
            display: none;
        }
        .rightTop{
            width: 100%;
        }
    }
}

