.AboutMe{
    clear: both;
    width: 100%;
    box-shadow : 0 4px 6px -1px rgba(0,0,0,0.1),0 2px 4px -1px rgba(0,0,0,0.06);
    height: 500px;
    .contents{
        width: 100%;
        margin-top: 100px;
        display: flex;
        justify-content: space-around;
        .item-box{
            background: #fff;
            overflow: hidden;
            width: calc((100% - 120px)/5);
            max-width: 250px;
            border-radius: 15px;
            padding: 20px;
            display: flex;
            justify-content: center;
            align-items: center;
            box-shadow : 0 4px 6px -1px rgba(0,0,0,0.1),0 2px 4px -1px rgba(0,0,0,0.06);
            .item{
                position: relative;
                top: 0;
                width: 80%;
                height: 100%;
                text-align: center;
                font-size: 1.6rem;
                .item-image{
                    img{
                        width: 70%;
                    }
                }
                .item-text{
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                }
                .item-text.position{font-size: 1.4rem;}
                .item-text.email{font-size: 1.2rem;}
                    
            }
        }
    }
}


/* 태블릿 */
@media screen and (max-width: 1023px) {
    .AboutMe{
        .contents{
            margin-top: 10px;
            flex-wrap: wrap;
            align-content: stretch;
            .item-box{
                width: calc(100%/4);
                margin: 20px;
                .item{
                    .item-image{
                        img{
                            width: 50%;
                        }
                    }
                }
            }
        }
    }
}

/* 모바일 */
@media screen and (max-width: 767px) {
    .AboutMe{
        height: 430px;
        .contents{
            margin-top: 20px;
            .item-box{
                width: calc(100%/3);
                margin: 8px;
            }
        }
    }
}

@media screen and (max-width: 415px) {
    .AboutMe{
        height: 380px;
        .contents{
            .item-box{
                width: calc(100%/4);
            }
        }
    }
}