.TopMenu{
    width: 100%;
    height: 80px;
    line-height: 80px;
    display: flex;
    justify-items: flex-end;
    color: #4C4C4C;
    ul{
        width: 100%;
        height: 80px;
        display: flex;
        justify-content: space-between;
        li{
            width: calc(100%/5);
            height: auto;
            cursor: pointer;
            list-style: none;
            font-size: 2.4rem;
            text-align: center;
        }
        li.animate{
            animation-name: titleBackground;
            animation-duration: 2s;
            animation-iteration-count: infinite;
        }
    }
    a{
        font-family: "DoHyeonRegular";
        text-decoration: none;
        color: #4C4C4C;
    }
    a:visited{
        color: #4C4C4C;
    }
}

@keyframes titleBackground {
    0% {
        background: #eee;
        color: #000;
    }
    25% {
        background: #fff;
        color: #fff;
    }
    50% {
        background: #eee;
        color: #000;
    }
    75%{
        background: #F0EEE2;
        color: #fff;
    }
    100%{
        background: #eee;
        color: #000;
    }
}