.Footer{
    clear: both;
    width: 100%;
    height: 140px;
    background: #39424C;
    .left{
        width: calc(50% - 60px);
        float: left;
        padding: 20px;
    }
    .right{
        width: calc(50% - 60px);
        float: right;
        padding: 20px;
    }
    p{
        font-size: 14px;
        margin: 8px 0;
        color: #eee;
    }
    a{
        text-decoration: none;        
        color: #fff;
    }
    a:visited{
        color: #fff;
    }
    .mobileMode{
        display: none;
    }
}

/* 태블릿 */
@media screen and (max-width: 1023px) {
    .Footer{
        height: 82px;
        .pcMode{
            display: none;
        }
        .mobileMode{
            display: block;
            padding: 5px;
            p{
                text-align: center;
                font-size: 14px;
                margin: 8px 0;
                color: #eee;
            }
            .iconArea{
                width: 100%;
                text-align: center;
                svg{
                    width: 32px;
                    margin: 5px;
                }
            }
        }
    }
}

/* 모바일 */
@media screen and (max-width: 767px) {
    .Footer{
        height: 82px;
    }
}