.wrapper{
    clear: both;
    width: 100%;
    height: 100%;
    .App{
        background: #eee;
        padding: 0 30px;
        display: flex;
        flex-direction: column;
        div{
            .title{
                width: 100%;
                padding-top: 50px;
                h3{                        
                    text-align: center;
                    display: block;
                    .logo{
                        font-size: 4rem;
                        line-height: 4rem;
                        svg{
                            vertical-align: middle;
                        }
                    }
                }
            }   
        }
        a{
            text-decoration: none;    
        }
        a:visited {
            color: #4C4C4C;
        }        
    }    
}

/* 모바일 */
@media screen and (max-width: 430px) {
    .wrapper{
        .App{
            div{
                .title{
                    padding-top: 30px;
                    .logo{
                        b{display: none;}
                    }
                }   
            }
        }    
    }
}
