@font-face {
  font-family: 'NotoSansKR-Regular';
  src: url('assets/font/NotoSansKR-Regular.otf');
}
@font-face {
  font-family: 'NotoSansKR-Medium';
  src: url('assets/font/NotoSansKR-Medium.otf');
}
@font-face {
  font-family: 'DoHyeonRegular';
  src: url('assets/font/DoHyeon-Regular.ttf');
}

html{font-size: 10px;}
* {
  margin: 0;
  padding: 0;
  font-family: 'NotoSansKR-Regular';
  font-weight: normal;
  font-style: normal;  
}
#root{
  width: 100%;
  height: 100%;
  position: relative;
}


@media screen and (max-width: 1023px) {
  html{font-size: 9px;}
}

/* 모바일 */
@media screen and (max-width: 767px) {
  html{font-size: 8px;}
}